.technology .carousel-item {
  height: 20rem;
  /* background: rgb(85, 42, 42); */
  background: #37517e;
  color: white;
  position: relative;
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.technology .text {
  position: absolute;
  bottom: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* right: 50px; */
}

.technology .overlay-image {
  background-image: url("../img/bg-product.jpg");
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  background-position: center;
  background-size: cover;
  opacity: 0.1;
}

#technology .content {
  margin-top: 30px;
}

#technology .content h1 {
  font-weight: bold;
  font-size: 60px;
  letter-spacing: 2px;
}

#technology .content h1 code {
  color: #37517e;
}

#technology .content p {
  margin-top: 30px;
  font-size: 20px;
}

.technology-image {
  width: 100%;
  margin-top: 40px;
  margin-bottom: 70px;
}
#technology .tech-category {
  margin-top: 80px;
  padding: 40px;
  margin-bottom: 80px;
}
#technology .head h2 {
  margin: 40px 0px 60px 30px;
}

#technology .icon-box {
  box-shadow: 0px 0 25px 0 rgba(0, 0, 0, 0.1);
  padding: 25px 30px;
  transition: all ease-in-out 0.4s;
  background: #fff;
  border-radius: 20px;
  width: 100%;
  text-align: center;
  height: 170px;
}

#technology .icon-box .icon {
  margin-bottom: 10px;
}

#technology .icon-box .icon img {
  width: 75px;
  height: 75px;
}

#technology .icon-box .icon i {
  color: #47b2e4;
  font-size: 36px;
  transition: 0.3s;
}

#technology .icon-box h4 {
  font-weight: 500;
  font-size: 24px;
}

#technology .icon-box h4 a {
  color: #37517e;
  transition: ease-in-out 0.3s;
}

#technology .icon-box:hover {
  transform: translateY(-10px);
}

#technology .icon-box:hover h4 a {
  color: #47b2e4;
}

@media (max-width: 900px){
  #technology .content h1 {
    font-size: 30px;
  }
  #technology .content p {
    font-size: 15px;
  }
}