.career .carousel-item {
  height: 20rem;
  background: #37517e;
  color: white;
  position: relative;
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.career .text h1 {
  font-size: 50px;
}
.career .text {
  position: absolute;
  bottom: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.career .text span {
  color: transparent;
  -webkit-text-stroke: 1px #fff;
}

.career .overlay-image {
  background-image: url("../img/bg-product.jpg");
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  background-position: center;
  background-size: cover;
  opacity: 0.1;
}

#career .content {
  margin-top: 80px;
  padding: 40px;
  margin-bottom: 80px;
}
#career .head h1 {
  margin: 40px 0px 60px 10px;
  color: #37517e;
  font-size: 50px;
}
#career .head h1 span {
  font-weight: lighter;
  color: black;
}

#career h3{
  font-size: 100px;
  text-align: center;
  margin-top: 50px;
  color: #44434354;
}

#career .icon-box {
  box-shadow: 0px 0 25px 0 rgba(0, 0, 0, 0.1);
  padding: 25px 30px;
  transition: all ease-in-out 0.4s;
  background: #fff;
  border-radius: 8px;
  width: 100%;
  text-align: center;
  height: auto;
}
#career .icon-box h4 {
  font-weight: 500;
  font-size: 24px;
  margin-top: 20px;
}

#career .icon-box h4 a {
  color: #37517e;
  transition: ease-in-out 0.3s;
  text-align: center;
}
#career .icon-box h4 p {
  font-size: 15px;
  margin-top: 30px;
  color: #444343;
}

#career .icon-box hr {
  border: 2px dotted;
  margin-top: 30px;
}

#career .icon-box:hover {
  transform: translateY(-10px);
}

#career .icon-box:hover h4 a {
  color: #47b2e4;
}

.form-header{
  font-size: 35px;
  color: #686767;
}


.form-button{
  display: flex;
  justify-content: center;
  align-items: center;
} 

.form-button button{
  width: 50%;
} 

@media (max-width:900px){
    
.career .text h1 {
    font-size: 40px;
  }
  #career .content {
    margin-top: 0px;
    padding: 20px;
    margin-bottom: 20px;
  }
  
#career .head h1 {
    margin: 40px 0px 30px 10px;
    font-size: 30px;
  }
  
#career .icon-box h4 {
    font-size: 20px;
  }
  
}