
#Projects .Projects {
    width: 100%;
    height: 80vh;
    background: #37517e;
  }
  
  #Projects .content {
    padding-top: 72px;
    position: relative;
  }
  
  #Projects h1 {
    margin: 0 0 10px 0;
    font-size: 60px;
    font-weight: 700;
    line-height: 56px;
    color: #fff;
  }
  
  #Projects h2 {
    color: rgba(255, 255, 255, 0.6);
    margin-bottom: 50px;
    font-size: 35px;
  }
  
  
  #Projects .image {
    display: block;
    width: 100%;
    height: auto;
    border-radius: 30px;
  }
  
  #Projects .overlay {
    position: absolute;
    bottom: 0px;
    left: 0;
    right: 0;
    width: 100%;
    height: 0;
    background-color: #37517e73;
    transition: 0.5s ease;
    border-radius: 30px;
  }
  
  #Projects .margin{
    margin: 50px;
  }
  #Projects .image-container:hover .overlay {
    height: 100%;
  }
  
  #Projects .text {
    color: white;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
    height: 100px;
  }
  
  @media (max-width: 991px) {
    #Projects {
      height: 80vh;
      text-align: center;
    }
  
    #Projects .home-img {
      text-align: center;
    }
  
    #Projects .home-img img {
      width: 50%;
    }
    
    #Projects .image {
      border-radius: 5px;
    }
    #Projects .overlay {
      border-radius: 5px;
    }
    
    #Projects .margin{
      margin: 20px;
    }
  }
  
  @media (max-width: 768px) {
    #Projects h1 {
      font-size: 28px;
      line-height: 36px;
    }
  
    #Projects h2 {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 30px;
    }
  
    #Projects .home-img img {
      width: 70%;
    }
  }
  
  @media (max-width: 575px) {
    #Projects .home-img img {
      width: 80%;
    }
  }