.image-container {
  min-height: 280px;
  max-height: 280px;
  background-color: #fff;
  text-align: center;
  overflow: hidden;
  box-shadow: 1px 1px 10px #aaa;
}

.image-container img {
  width: 100%;
  height: 200px;
  margin-bottom: 25px;
  background-image: linear-gradient(to right, #000 , #aaa);
}
