.products-inline {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  height: 380px;
  overflow: hidden;
}

.products-inline > div {
  width: 50%;
}

.products-inline .full-image {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fdfdfd;
  height: 100%;
}

.products-inline .full-image img {
  width: 100%;
}

.products-submenu {
  position: absolute;
  top: 72.5px;
  right: 5%;
  list-style: none;
  text-align: start;
  display: block !important;
  width: 800px;
  min-height: 380px;
  background: whitesmoke;
  max-height: auto;
}
.products-submenu h3 {
  text-align: center;
  letter-spacing: 3px;
  font-weight: lighter;
  margin-top: 20px;
}

.products-names {
  margin-top: 50px;
  letter-spacing: 4px;
}

.products-submenu.clicked {
  display: none;
}

.products-list {
  background: whitesmoke;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.products-list:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

.products-list span {
  display: block;
  text-decoration: none;
  color: #37517e !important;
  font-size: 18px;
}

.products-list img {
  width: 25px;
  height: 25px;
  margin-right: 20px;
}
.products-inline button {
  align-items: center;
  justify-items: center;
  margin-left: 30%;
  margin-top: 60px;
}

.client-png {
  width: 100%;
  height: 100%;
}

