#Products {
  padding-top: 0rem;
}
#Products .carousel-item {
  height: 30rem;
  /* background: rgb(85, 42, 42); */
  background: #37517e;
  color: white;
  position: relative;
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

#Products .content {
  position: absolute;
  bottom: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

#Products .overlay-image {
  background-image: url("../img/bg-product.jpg");
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  background-position: center;
  background-size: cover;
  opacity: 0.1;
}

#Products .image {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 30px;
}

#Products .overlay {
  position: absolute;
  bottom: 0px;
  left: 0;
  right: 0;
  width: 100%;
  height: 0;
  background-color: #37517e73;
  transition: 0.5s ease;
  border-radius: 30px;
}

#Products .margin {
  margin: 50px;
}

#Product .image-container{
  height: 280px;
  background-color: #fff;
  text-align: center;
  overflow: hidden;
  box-shadow: 1px 1px 10px #aaa;
}

#Products .image-container img{
  width: 100%;
  height: 200px;
  margin-bottom: 25px;
}

#Products .image-container:hover .overlay {
  height: 100%;
}

#Products .text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  height: 100px;
}

@media (max-width: 991px) {
  #Products .carousel-caption p {
    margin-bottom: 1.25rem;
    font-size: 1.25rem;
    line-height: 1.4;
  }
  #Products .content {
    position: absolute;
    bottom: 120px;
    margin-left: 550px;
    right: 0;
  }
  #Products .image {
    border-radius: 5px;
  }
  #Products .overlay {
    border-radius: 5px;
  }

  #Products .margin {
    margin: 20px;
  }
}
