.disc .carousel-item {
  height: 20rem;
  /* background: rgb(85, 42, 42); */
  background: #37517e;
  color: white;
  position: relative;
  background-position: center;
  background-size: cover;
}

.disc .overlay-image {
  background-image: url("../img/bg-product.jpg");
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  background-position: center;
  background-size: cover;
  opacity: 0.5;
}

.jobHead {
  margin-top: 40px;
  align-items: center;
  justify-content: space-between;
}

.jobHead h2{
  color: #37517e;
}

.content{
  padding: 40px;
}

.content p{
  color: rgb(121, 119, 119);
}

.button {
  border-radius: 5px;
  background-color: white;
  border: 2px solid #47b2e4;
  color: #47b2e4;
  font-size: 20px;
  padding: 10px 20px;
}
.button:hover {
  background-color: #47b2e4;
  color: white;
}

.btnDiv {
  display: flex;
  align-items: center;
}

@media (max-width: 990px) {
  .button {
    width: 100%;
  }
  .button {
    background-color: #47b2e4;
    color: white;
  }
}
