.general-title {
    text-align: center;
    padding-bottom: 30px;
  }
  
  .general-title h2 {
    font-size: 32px;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 20px;
    padding-bottom: 20px;
    position: relative;
    color: #37517e;
  }
  
  .general-title h2::before {
    content: "";
    position: absolute;
    display: block;
    width: 120px;
    height: 1px;
    background: #ddd;
    bottom: 1px;
    left: calc(50% - 60px);
  }
  
  .general-title h2::after {
    content: "";
    position: absolute;
    display: block;
    width: 40px;
    height: 3px;
    background: #47b2e4;
    bottom: 0;
    left: calc(50% - 20px);
  }

  /* --------overview----- */

  .overview-bg {
    background-color: #f3f5fa;
  }
  
  .overview-title p {
    margin-bottom: 0;
  }

  /* ----------technical stack ------- */

  .tech-bg {
    background-color: white;
  }
  
  .tech .accordion-list {
    padding: 0 100px 20px 100px;
  }
  
  .tech .accordion-list ul {
    padding: 0;
    list-style: none;
  }
  
  .tech .accordion-list li + li {
    margin-top: 15px;
  }
  
  .tech .accordion-list li {
    background: #f3f5fa;
    border-radius: 4px;
  }
  
  .tech-list {
    position: relative;
    display: flex;
    width: 100%;
    align-items: center;
    background-color: #fff;
  }
  
  .tech-name {
    /* position: absolute;
    top: 0;
    left: 0; */
    background-color: #37517e;
    height: 100%;
    display: flex;
    align-items: center;
    color: #f3f5fa;
    font-size: 30px;
    padding: 20px;
    width: 250px;
    text-transform: uppercase;
  }
  
  .tech-items {
    margin-left: 280px;
  }
  
  @media (max-width: 1024px) {
    .tech .content,
    .tech .accordion-list {
      padding-left: 0;
      padding-right: 0;
    }
  }
  
  @media (max-width: 992px) {
    .tech .content {
      padding-top: 30px;
    }
  
    .tech .accordion-list {
      padding-bottom: 30px;
    }
    .tech-name {
      font-size: 15px;
      padding: 15px;
      width: 150px;
    }
    .tech-items {
      margin-left: 150px;
    }
  }

  /* -------------How It works---------------------------------------- */

  .work .content h3 {
    font-weight: 400;
    font-size: 34px;
    color: #37517e;
  }
  
  .work .content h4 {
    font-size: 20px;
    font-weight: 700;
    margin-top: 5px;
  }
  
  .work .accordion-list {
    padding: 0 100px 30px 100px;
  }
  
  .work .accordion-list ul {
    padding: 0;
    list-style: none;
  }
  
  .work .accordion-list li + li {
    margin-top: 15px;
  }
  
  .work .accordion-list div {
    padding: 20px;
    background: #fff;
    border-radius: 4px;
  }
  .work-list ol {
    list-style-type: circle;
  }
  
  @media (max-width: 1024px) {
    .work .content,
    .work .accordion-list {
      padding-left: 0;
      padding-right: 0;
    }
  }
  
  @media (max-width: 992px) {
    .work .img {
      min-height: 400px;
    }
  
    .work .content {
      padding-top: 30px;
    }
  
    .work .accordion-list {
      padding-bottom: 30px;
    }
  }

  /* ---------------- Core-features------------------- */
  .features-text {
    text-align: justify;
  }

  /* ---------------Developers---------------------------- */
 
#developers {
  display: flex;
  justify-items: center;
  align-items: center;
}

#developers .devCardRow {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 4px;
}

#developers .devCardRow .devCardCol > div {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #37517e;
  color: #f3f5fa;
  padding: 20px;
  margin: 20px 0px;
  height: 300px;
}

#developers .devCardRow .devCardCol:nth-child(even) > div {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f3f5fa;
  color: #37517e;
  padding: 20px;
  margin: 20px 0px;
  height: 300px;
  border: 1px solid #37517e;
}

#developers .icon {
  font-size: 40px;
  /* color: #f3f5fa; */
  margin-bottom: 20px;
}

#developers h1,
h2 {
  margin-bottom: 20px;
  font-weight: bolder;
}

/* ---------------------- Timeframe------------------ */
.timeFrame {
  margin-bottom: 40px;
}

.timeFrame .content {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #37517e;
  margin-bottom: 20px;
  padding: 30px;
  border: 1px solid #37517e;
  border-radius: 5px;
}
.timeFrame .date {
  margin-left: 140px;
}

@media (max-width: 600px) {
  .timeFrame .date {
    margin-left: 40px;
  }
  .timeFrame .text,
  .date {
    font-size: 2rem;
  }
}