.inline {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  height: 380px;
  overflow: hidden;
}

.inline > div {
  width: 50%;
}

.inline .full-image {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fdfdfd;
  height: 100%;
}

.inline .full-image img {
  width: 100%;
}

.customers-submenu {
  position: absolute;
  top: 60px;
  right: 0px;
  list-style: none;
  text-align: start;
  display: block !important;
  width: 100%;
  min-height: 380px;
  background: whitesmoke;
  max-height: auto;
}
.customers-submenu h3 {
  text-align: center;
  letter-spacing: 3px;
  font-weight: lighter;
  margin-top: 20px;
}

.customers-names {
  margin-top: 50px;
  letter-spacing: 4px;
  overflow: hidden;
  overflow-y: scroll;
  height: calc(380px + 45px - 50px - 100px);
}

.customers-submenu.clicked {
  display: none;
}

.customers-list {
  background: whitesmoke;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.customers-list:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

.customers-list span {
  display: block;
  text-decoration: none;
  color: #37517e !important;
  font-size: 18px;
}

.customers-list img {
  width: 25px;
  height: 25px;
  margin-right: 20px;
}
.inline button {
  align-items: center;
  justify-items: center;
  margin: 30px 100px;
}

.client-png {
  width: 100%;
  height: 100%;
}

@media (max-width: 1024px) {
  .inline,
  .customers-submenu {
    display: none !important;
  }
}
