.popup-footer {
    padding: 0;
    font-style: none;
    color: black;
  }
  .popup-footer ul {
    right: 20px;
    display: flex;
    align-items: center;
  }
  
  .popup-footer ul li {
    display: flex;
    align-items: center;
  }
  
  .popup-footer img {
    margin-left: 80px;
    width: 25px;
    height: 25px;
  }
  
  .popup-footer a {
    cursor: pointer;
    color: black;
  }
  
  .popup-footer a span:hover {
    color: #47b2e4;
  }
  